




































import { Vue, Component, Prop, Emit, Model, Watch } from 'vue-property-decorator';
import { IComponent } from '@/../models';

// vue-multiselect
import Multiselect from 'vue-multiselect';
import 'vue-multiselect/dist/vue-multiselect.min.css';

@Component({
    components: { Multiselect },
})
export class SearchMultiple extends Vue {
    // Prop
    @Prop({
        type: String, // Boolean, Number, String, Array, Object
        default: () => undefined,
    })
    private placeholder: string;

    // @Prop({
    //     type: Array, // Boolean, Number, String, Array, Object
    //     default: () => [],
    // })
    // private multipleData: ISelectionBase[];

    @Prop({
        type: Array, // Boolean, Number, String, Array, Object
        default: () => [],
    })
    private options: IComponent.ISelectionBase[];

    private modelData: IComponent.ISelectionBase[] = [];

    // @Watch('multipleData', { immediate: true })
    // private singleDataChanged(newval: ISelectionBase[], oldval: ISelectionBase[]): void {
    //     this.modelData = newval;
    // }

    @Watch('options', { immediate: true, deep: true })
    private optionsChanged(newval: IComponent.ISelectionBase[], oldval: IComponent.ISelectionBase[]): void {}

    private formSelectMultipleInputEvent(): void {
        this.$emit('select-multiple', this.modelData);
    }
}

export default SearchMultiple;
Vue.component('search-multiple', SearchMultiple);
