import { IComponent } from '@/../models';

import SearchInput from '@/../components/search/search_input.vue';
import SearchSingle from '@/../components/search/search_single.vue';
import SearchMultiple from '@/../components/search/search_multiple.vue';

interface ISearchData {
    keyword: string;
    single: IComponent.ISelectionBase;
    multiple: IComponent.ISelectionBase[];
}

interface ISearchOption {
    single: IComponent.ISelectionBase[];
    multiple: IComponent.ISelectionBase[];
}

// // Component
// import ServiceToast from '@/../services/toast';

@Component({
    components: { SearchInput, SearchSingle, SearchMultiple },
})
export default class VuePageClass extends Vue {
    private searchData: ISearchData = {
        keyword: '',
        single: null,
        multiple: [],
    };

    private searchOption: ISearchOption = {
        single: [],
        multiple: [],
    };

    //////////////////// Vue Life ////////////////////

    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {
        await this.initSelectOption();
    }
    private async mounted(): Promise<void> {}
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}

    //////////////////// Init Data ////////////////////
    private initSelectOption(): void {
        this.initSelectSingle();
        this.initSelectMultiple();
    }

    private initSelectSingle(): void {
        this.searchOption.single = [
            { objectId: 'single1', name: '曹政奭' },
            { objectId: 'single2', name: '柳演錫' },
            { objectId: 'single3', name: '鄭敬淏' },
            { objectId: 'single4', name: '金大明' },
            { objectId: 'single5', name: '田美都' },
        ];
    }

    private initSelectMultiple(): void {
        this.searchOption.multiple = [
            { objectId: 'multiple1', name: '李翊晙' },
            { objectId: 'multiple2', name: '安政源' },
            { objectId: 'multiple3', name: '金雋婠' },
            { objectId: 'multiple4', name: '楊碩亨' },
            { objectId: 'multiple5', name: '蔡頌和' },
        ];
    }

    //////////////////// Init Select Option  ////////////////////

    //////////////////// Search ////////////////////

    private searchInputKeyword(): void {
        console.log('search_input => ', this.searchData.keyword);
    }

    private searchSelectSingle(singleData: IComponent.ISelectionBase): void {
        this.searchData.single = singleData;
        console.log('search_single => ', this.searchData.single);
    }

    private searchSelectMultiple(multipleData: IComponent.ISelectionBase[]): void {
        this.searchData.multiple = multipleData;
        console.log('search_multiple => ', this.searchData.multiple);
    }

    //////////////////// Form Show ////////////////////

    //////////////////// Form Set Data ////////////////////

    //////////////////// Modify API (save, delete, test)  ////////////////////

    //////////////////// Resolve text ////////////////////

    //////////////////// Modal ////////////////////
}
