


































import { Vue, Component, Prop, Emit, Model, Watch } from 'vue-property-decorator';
import { IComponent } from '@/../models';

// vue-multiselect
import Multiselect from 'vue-multiselect';
import 'vue-multiselect/dist/vue-multiselect.min.css';

@Component({
    components: { Multiselect },
})
export class SearchSingle extends Vue {
    // Prop
    @Prop({
        type: String, // Boolean, Number, String, Array, Object
        default: () => undefined,
    })
    private placeholder: string;

    @Prop({
        type: Array, // Boolean, Number, String, Array, Object
        default: () => [],
    })
    private options: IComponent.ISelectionBase[];

    private modelData: IComponent.ISelectionBase = null;

    @Watch('options', { immediate: true, deep: true })
    private optionsChanged(newval: IComponent.ISelectionBase[], oldval: IComponent.ISelectionBase[]): void {}

    private selectSingleInputEvent(): void {
        this.$emit('select-single', this.modelData);
    }
}

export default SearchSingle;
Vue.component('search-single', SearchSingle);
